.btn {
  position: relative;
  display: inline-flex;
  color: #fff;
  align-items: center;
  cursor: pointer;
  max-width: 12.625rem;
  width: 100%;
  font-size: 0.875rem;
  line-height: 1.5714285714;
  font-weight: 400;
  font-family: "Cardo", serif;
  color: white;
  padding: 1rem 2.5rem;
  margin-bottom: 0.25rem;
  background-color: #6E97CD;
  z-index: 1;
}

.btn span::first-letter {
  text-transform: uppercase;
}

.btn::before {
  border: 1px solid #6E97CD;
  content: "";
  height: 100%;
  right: -4px;
  top: 4px;
  position: absolute;
  z-index: -999;
  width: 100%;
}

.btn span::first-letter {
  text-transform: uppercase;
}

.btn::after {
  background: #fff;
  border: 1px solid #6E97CD;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -10;
}

.btn:hover::after {
  transform: scale(1, 1);
}

.btn__arrow {
  margin-left: 0.5rem;
  width: 2.5rem;
  height: 0.375rem;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: skew(45deg);
}

.btn:hover span {
  color: #6E97CD;
}

.btn:hover .btn__arrow {
  margin-left: 0.5rem;
  width: 2.5rem;
  height: 0.375rem;
  border-bottom: 1px solid #6E97CD;
  border-right: 1px solid #6E97CD;
  transform: skew(45deg);
}

.btn:hover {
  color: #6E97CD;
  opacity: 1;
  z-index: 10;
}

.btn--white {
  background-color: #fff;
  color: #6E97CD;
}

.btn--white::before {
  border: 1px solid #fff;
}

.btn__arrow.btn__arrow--green {
  border-bottom: 1px solid #6E97CD;
  border-right: 1px solid #6E97CD;
}

.btn--white::after {
  background-color: #6E97CD;
  border: 1px solid #fff;
}

.btn--white:hover span {
  color: #fff;
}

.btn--white:hover .btn__arrow--green {
  margin-left: 0.5rem;
  width: 2.5rem;
  height: 0.375rem;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: skew(45deg);
}

.btn--white span::last-letter {
  text-transform: uppercase; /* 最後の文字を大文字にする */
}

.btn__arrow {
  margin-left: 8px;
  width: 40px;
  height: 6px;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: skew(45deg);
  z-index: 2; /* ボタンのテキストを最前面に */
  transition: border-color 0.4s ease-in-out;
}

.btn__arrow--contact {
  position: absolute;
  margin-left: 1.0625rem;
  width: 2.5rem;
  z-index: 2;
  top: 46%;
  left: 50%;
}

