.fv {
  width: 100%;
  height: 100svh;
  position: relative;
}

.fv__inner {
  width: 100%;
  height: inherit;
}

.fv__swiper,
.fv__swiper-slide picture,
.fv__swiper-slide img {
  object-fit: cover;
  height: inherit;
}
@keyframes zoomUp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.15);
  }
}
.swiper-slide-active .fv__swiper-slide img,
.swiper-slide-duplicate-active .fv__swiper-slide img,
.swiper-slide-prev .fv__swiper-slide img {
  animation: zoomUp 7s linear 0s normal both;
}

.fv__swiper-slide {
  display: block;
  height: 100svh;
  width: 100%;
}

.fv__title-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
